.exeperience_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.exeperience_container > div {
    background: var(--color-bg-varient);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.exeperience_container > div:hover {
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}

.exeperience_container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.exeperience_content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.exeprience_details {
    display: flex;
    gap: 1rem;
}

.exeperinece_details-icon {
    margin-top: 6px;
    font-size: 30px;
    color: var(--color-primary);
}

@media screen and (max-width: 1024px) {
    .exeperience_container {
        grid-template-columns: 1fr;
    }

    .exeperience_container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .exeperience_content {
        padding: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .exeperience_container {
        gap: 1rem;
    }

    .exeperience_container > div {
        width: 100%;
        padding: 2rem 1rem;
    }
}

